.card{
    position: relative;
    padding: 20px ;
    margin: 0 auto;
    width: 600px;
    max-width: 85vw;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    /*  */
    /* background-color: yellow; */
    
}

.tinderCard__cardContainter{
    display: flex;
    justify-content: center;
   
    
    
}

.swipe{
    position: absolute;
    box-shadow: 0px 18px 53px 0px rgba(90, 41, 41, 0.3);
    border-radius: 20px;
    
}
.card h3{
    position: absolute;
    color: white;
    bottom: 10px;
}