.chatScreen > p{
    text-align: center;
    color: gray;
}

.chatScreen__message{
    padding: 20px;
    display: flex;
    margin: 5px;
    align-items: center;
}

.chatScreen__text{
    margin-left: 15px;
    background-color: lightgrey;
    padding: 10px;
    border-radius: 20px;
}

.chatScreen__textUser{
    margin-left: auto;
    background-color: #29b3cd;
    border-radius: 20px;
    padding: 15px;
    color: white;

}.chatScreen__input{
    bottom: 0;
    display: flex;
    position: fixed;
    padding: 5px;
    width: 100%;
    border-top: 1px solid lightgray;
}

.chatScreen__inputFeild{
    flex: 1;
    padding: 5px;
    border:none;
}
.chatScreen__inputButton{
    border: none;
    font-weight: bold;
    color: #fe3d71;
    background-color: white;
    margin-right: 5px;
}