.chat{
    display: flex;
    justify-content: space-between;
     align-items: center;
     padding: 20px;
     height: 70px;
     border-bottom: 1px solid rgb(250, 244, 244);
    
}

a{
    text-decoration: none;
    color: inherit;
}

.chat__details{
    flex: 1;
}
.image{
    margin-right: 15px;
}
.chat__details > p{
    color: gray;
}

.chat__timeStamp{
    color: lightgray;
}